import React, { Component } from 'react';
import { Menu, } from 'antd';
import { withRouter } from "react-router-dom";
import { inject, } from 'mobx-react';
import { permissions } from '../utils/fun'
import Icon from '../icon'
const SubMenu = Menu.SubMenu;

@inject(({ authStore, appStore }) => ({
  addTab: appStore.addTab,
}))
@withRouter
class NavMenu extends Component {
  state = {
    openKeys: ['0'],
  };

  onOpenChange = openKeys => {
    let { menuTree = [] } = this.props;
    const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
    if (menuTree.map(item => item._id).indexOf(latestOpenKey) === -1) {
      this.setState({ openKeys });
    } else {
      this.setState({
        openKeys: latestOpenKey ? [latestOpenKey] : [],
      });
    }
  };

  changeMenu = e => {
    const { url, title, module } = e.item.props
    if (url) {
      this.props.addTab({
        url,
        title,
        module
      })
      this.props.history.push(url || '/')
    } else {
      if (url.includes('http')) {
        window.location.href = url
      } else {
        const params = this.props.match.params
        let _url = url
        for (let key in params) {
          _url = _url.replace(new RegExp(`{${key}}`, 'g'), params[key])
        }
        this.props.history.push(_url || '/')
      }
    }
  }

  getSubMenu = (data) => {
    return data.map((item, key) => {
      // console.warn(permissions(item.path), item.path);
      return (permissions(item.path) || !item.path) ? (
        item.children ?
          <SubMenu key={item._id} title={item.title}>
            {
              this.getSubMenu(item.children)
            }
          </SubMenu>
          :
          <Menu.Item key={item._id} title={item.title} url={item.url} module={item.moduleName}>{item.title}</Menu.Item>
      ) : null
    });
  }

  getIcon = key => {
    const { collapsed } = this.props
    if (key) {
      return <Icon iconName={key} size={16} marginRight={collapsed ? 50 : 5} style={{ marginTop: -3 }} />
    } else {
      return null
    }
  }

  render() {
    let { menuTree = [], activeKey, openKeys, style = {}, theme, collapsed } = this.props;
    return (
      <Menu
        mode="inline"
        openKeys={this.state.openKeys.length ? this.state.openKeys : openKeys}
        defaultSelectedKeys={[activeKey]}
        theme={theme}
        onClick={this.changeMenu}
        onOpenChange={this.onOpenChange}
        // inlineCollapsed={inlineCollapsed}
        style={{ borderRight: 'none', ...style }}
      >
        {
          menuTree.length ? menuTree.map((item, key) => {
            // console.warn(item.moduleName, checkPermissions(permissions, item.moduleName));
            return (
              (permissions(item.path) || !item.path) ? (
                item.children ?
                  <SubMenu key={item._id} title={<span>{item.icon && this.getIcon(item.icon, collapsed)}<span>{item.title}</span></span>}>
                    {
                      this.getSubMenu(item.children)
                    }
                  </SubMenu>
                  :
                  <Menu.Item key={item._id} title={item.title} url={item.url} module={item.moduleName}>
                    {item.icon && this.getIcon(item.icon)}
                    <span className="nav-text">{item.title}</span>
                  </Menu.Item>
              ) : null
            )
          }) : null
        }
      </Menu>
    );
  }
}

export default NavMenu;
